<template>
  <div class="weather-view">
    <header class="page-header">
      <h1 class="page-title">Â§©Ê∞îÈ¢ÑÊä•</h1>
      <p class="page-description">ÂÆûÊó∂Â§©Ê∞î‰ø°ÊÅØÔºå‰∏∫ÊÇ®Êèê‰æõÂáÜÁ°ÆÁöÑÂ§©Ê∞îÈ¢ÑÊä•ÊúçÂä°</p>
    </header>

    <main class="main-content">
      <!-- ÊêúÁ¥¢Ê°Ü -->
      <div class="search-container">
        <div class="search-box">
          <input
            v-model="searchCity"
            type="text"
            placeholder="ËæìÂÖ•ÂüéÂ∏ÇÂêçÁß∞ÔºåÂ¶ÇÔºöÂåó‰∫¨„ÄÅShanghai„ÄÅTokyo"
            class="search-input"
            @keyup.enter="searchWeather"
          />
          <button class="search-button" @click="searchWeather">
            <span>ÊêúÁ¥¢</span>
          </button>
        </div>
        <div class="location-buttons">
          <button class="location-btn" @click="getCurrentLocation">
            <span>üìç</span> Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ
          </button>
        </div>
      </div>

      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner"></div>
        <p>Ê≠£Âú®Âä†ËΩΩÂ§©Ê∞îÊï∞ÊçÆ...</p>
      </div>

      <!-- ÈîôËØØÊèêÁ§∫ -->
      <div v-if="error" class="error-container">
        <p>{{ error }}</p>
        <button class="retry-button" @click="retry">ÈáçËØï</button>
      </div>

      <!-- Â§©Ê∞î‰ø°ÊÅØÂç°Áâá -->
      <div v-if="weatherData && !loading" class="weather-container">
        <!-- ÂΩìÂâçÂ§©Ê∞î -->
        <div class="current-weather-card">
          <div class="weather-header">
            <div class="location-info">
              <h2 class="city-name">{{ weatherData.city }}</h2>
              <p class="location-detail">{{ weatherData.country }}</p>
            </div>
            <div class="weather-icon">
              <span v-if="weatherData.icon && weatherData.icon.startsWith('http')" class="icon-wrapper">
                <img
                  :src="weatherData.icon"
                  :alt="weatherData.description"
                  class="icon-img"
                />
              </span>
              <span v-else class="icon-emoji">{{ weatherData.icon || '‚òÄÔ∏è' }}</span>
            </div>
          </div>

          <div class="temperature-section">
            <div class="main-temp">{{ weatherData.temperature }}¬∞C</div>
            <div v-if="weatherData.feelsLike !== weatherData.temperature" class="feels-like">‰ΩìÊÑüÊ∏©Â∫¶: {{ weatherData.feelsLike }}¬∞C</div>
            <div class="description">{{ weatherData.description }}</div>
          </div>

          <div class="weather-details">
            <div class="detail-item">
              <span class="detail-label">ÊπøÂ∫¶</span>
              <span class="detail-value">{{ weatherData.humidity }}%</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">È£éÈÄü</span>
              <span class="detail-value">{{ weatherData.windSpeed }} m/s</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">Ê∞îÂéã</span>
              <span class="detail-value">{{ weatherData.pressure }} hPa</span>
            </div>
            <div class="detail-item">
              <span class="detail-label">ËÉΩËßÅÂ∫¶</span>
              <span class="detail-value">{{ weatherData.visibility }} km</span>
            </div>
          </div>
        </div>

        <!-- Á©øË°£Êé®Ëçê -->
        <div v-if="weatherData && weatherData.clothingAdvice && !loading" class="clothing-advice-card">
          <div class="advice-header">
            <h3 class="advice-title">üëî Á©øË°£Êé®Ëçê</h3>
            <span class="advice-level" :class="getLevelClass(weatherData.clothingAdvice.level)">
              {{ weatherData.clothingAdvice.level }}
            </span>
          </div>
          
          <div class="advice-content">
            <div class="outfit-section">
              <h4 class="section-title">Êé®ËçêÁ©øÊê≠</h4>
              <div class="outfit-tags">
                <span
                  v-for="(item, index) in weatherData.clothingAdvice.outfit"
                  :key="index"
                  class="outfit-tag"
                >
                  {{ item }}
                </span>
              </div>
            </div>
            
            <div v-if="weatherData.clothingAdvice.tips && weatherData.clothingAdvice.tips.length > 0" class="tips-section">
              <h4 class="section-title">Ê∏©È¶®ÊèêÁ§∫</h4>
              <ul class="tips-list">
                <li
                  v-for="(tip, index) in weatherData.clothingAdvice.tips"
                  :key="index"
                  class="tip-item"
                >
                  {{ tip }}
                </li>
              </ul>
            </div>
          </div>
        </div>

        <!-- Êú™Êù•Âá†Â§©È¢ÑÊä• -->
        <div v-if="forecastData && forecastData.length > 0" class="forecast-section">
          <h3 class="forecast-title">Êú™Êù•Âá†Â§©È¢ÑÊä•</h3>
          <div class="forecast-list">
            <div
              v-for="(day, index) in forecastData"
              :key="index"
              class="forecast-card"
            >
              <div class="forecast-date">{{ day.date }}</div>
              <div class="forecast-icon">
                <span v-if="day.icon && day.icon.startsWith('http')" class="forecast-icon-wrapper">
                  <img :src="day.icon" :alt="day.description" />
                </span>
                <span v-else class="forecast-icon-emoji">{{ day.icon || '‚òÄÔ∏è' }}</span>
              </div>
              <div class="forecast-temp">
                <span class="temp-high">{{ day.tempMax }}¬∞</span>
                <span class="temp-low">/ {{ day.tempMin }}¬∞</span>
              </div>
              <div class="forecast-desc">{{ day.description }}</div>
            </div>
          </div>
        </div>
      </div>

      <!-- ÈªòËÆ§ÊèêÁ§∫ -->
      <div v-if="!weatherData && !loading && !error" class="welcome-container">
        <div class="welcome-icon">‚òÄÔ∏è</div>
        <h3>Ê¨¢Ëøé‰ΩøÁî®Â§©Ê∞îÈ¢ÑÊä•</h3>
        <p>ËØ∑ËæìÂÖ•ÂüéÂ∏ÇÂêçÁß∞ÊàñÁÇπÂáª"Ëé∑ÂèñÂΩìÂâç‰ΩçÁΩÆ"Êù•Êü•ÁúãÂ§©Ê∞î‰ø°ÊÅØ</p>
      </div>
    </main>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'WeatherView',
  data() {
    return {
      searchCity: '',
      weatherData: null,
      forecastData: [],
      loading: false,
      error: null,
      // ‰ΩøÁî®ÂÖçË¥πÁöÑ OpenWeatherMap API
      // ËØ∑ËÆøÈóÆ https://openweathermap.org/api Ê≥®ÂÜåËé∑ÂèñÂÖçË¥πÁöÑ API Key
      // ÊàñËÄÖ‰ΩøÁî® Open-Meteo APIÔºàÊó†ÈúÄAPI keyÔºåÂÆåÂÖ®ÂÖçË¥πÔºâ
      apiKey: '', // Â¶ÇÊûú‰ΩøÁî® OpenWeatherMapÔºåËØ∑Âú®ËøôÈáåÂ°´ÂÖ•ÊÇ®ÁöÑ API Key
      useOpenWeatherMap: false // ËÆæÁΩÆ‰∏∫ true ‰ΩøÁî® OpenWeatherMapÔºåfalse ‰ΩøÁî® Open-Meteo
    }
  },
  mounted() {
    // ÈªòËÆ§Âä†ËΩΩÂåó‰∫¨Â§©Ê∞î
    this.searchCity = 'Âåó‰∫¨'
    this.searchWeather()
  },
  methods: {
    async searchWeather() {
      if (!this.searchCity.trim()) {
        this.error = 'ËØ∑ËæìÂÖ•ÂüéÂ∏ÇÂêçÁß∞'
        return
      }

      this.loading = true
      this.error = null

      try {
        if (this.useOpenWeatherMap && this.apiKey) {
          await this.fetchOpenWeatherMap()
        } else {
          await this.fetchOpenMeteo()
        }
      } catch (err) {
        this.error = 'Ëé∑ÂèñÂ§©Ê∞îÊï∞ÊçÆÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñÁ®çÂêéÈáçËØï'
        console.error('Weather API Error:', err)
      } finally {
        this.loading = false
      }
    },

    async fetchOpenMeteo() {
      // ‰ΩøÁî® Open-Meteo APIÔºàÂÖçË¥πÔºåÊó†ÈúÄAPI keyÔºâ
      // È¶ñÂÖàÈÄöËøáÂú∞ÁêÜÁºñÁ†ÅAPIËé∑ÂèñÂüéÂ∏ÇÂùêÊ†á
      const city = encodeURIComponent(this.searchCity)
      
      // ‰ΩøÁî® Open-Meteo ÁöÑÂú∞ÁêÜÁºñÁ†ÅAPIËé∑ÂèñÂùêÊ†á
      const geocodeResponse = await axios.get(
        `https://geocoding-api.open-meteo.com/v1/search?name=${city}&count=1&language=zh&format=json`,
        { timeout: 10000 }
      )

      if (!geocodeResponse.data.results || geocodeResponse.data.results.length === 0) {
        throw new Error('Êú™ÊâæÂà∞ËØ•ÂüéÂ∏Ç')
      }

      const location = geocodeResponse.data.results[0]
      const latitude = location.latitude
      const longitude = location.longitude
      const cityName = location.name
      const country = location.country || ''

      // Ëé∑ÂèñÂΩìÂâçÂ§©Ê∞îÂíåÈ¢ÑÊä•
      const weatherResponse = await axios.get(
        `https://api.open-meteo.com/v1/forecast?latitude=${latitude}&longitude=${longitude}&current=temperature_2m,relative_humidity_2m,weather_code,wind_speed_10m,pressure_msl,visibility&daily=weather_code,temperature_2m_max,temperature_2m_min&timezone=Asia%2FShanghai&forecast_days=6`,
        { timeout: 10000 }
      )

      const data = weatherResponse.data
      const current = data.current

      // Â§©Ê∞î‰ª£Á†ÅËΩ¨‰∏≠ÊñáÊèèËø∞
      const weatherDescriptions = {
        0: 'Êô¥Êúó', 1: 'Â§ßÈÉ®ÂàÜÊô¥Êúó', 2: 'ÈÉ®ÂàÜÂ§ö‰∫ë', 3: 'Èò¥Â§©',
        45: 'Èõæ', 48: 'Ê≤âÁßØÈúúÈõæ',
        51: 'Â∞èÈõ®', 53: '‰∏≠Èõ®', 55: 'Â§ßÈõ®',
        56: 'ÂÜªÈõ®', 57: 'Âº∫ÂÜªÈõ®',
        61: 'Â∞èÈõ®', 63: '‰∏≠Èõ®', 65: 'Â§ßÈõ®',
        66: 'ÂÜªÈõ®', 67: 'Âº∫ÂÜªÈõ®',
        71: 'Â∞èÈõ™', 73: '‰∏≠Èõ™', 75: 'Â§ßÈõ™',
        77: 'Èõ™Á≤í',
        80: 'Â∞èÈõ®', 81: '‰∏≠Èõ®', 82: 'Â§ßÈõ®',
        85: 'Â∞èÈõ™', 86: 'Â§ßÈõ™',
        95: 'Èõ∑Êö¥', 96: 'Èõ∑Êö¥‰º¥ÂÜ∞Èõπ', 99: 'Âº∫Èõ∑Êö¥‰º¥ÂÜ∞Èõπ'
      }

      const weatherCode = current.weather_code
      const description = weatherDescriptions[weatherCode] || 'Êú™Áü•'

      const temp = Math.round(current.temperature_2m)
      const windSpeed = Math.round(current.wind_speed_10m * 10) / 10
      
      this.weatherData = {
        city: cityName,
        country: country,
        temperature: temp,
        feelsLike: Math.round(current.temperature_2m), // Open-Meteo ‰∏çÊèê‰æõ‰ΩìÊÑüÊ∏©Â∫¶Ôºå‰ΩøÁî®ÂΩìÂâçÊ∏©Â∫¶
        description: description,
        humidity: current.relative_humidity_2m,
        windSpeed: windSpeed,
        pressure: Math.round(current.pressure_msl),
        visibility: current.visibility ? (current.visibility / 1000).toFixed(1) : 'N/A',
        icon: this.getWeatherIcon(weatherCode),
        weatherCode: weatherCode,
        clothingAdvice: this.getClothingAdvice(temp, weatherCode, windSpeed, current.relative_humidity_2m)
      }

      // Ëé∑ÂèñÊú™Êù•Âá†Â§©ÁöÑÈ¢ÑÊä•
      if (data.daily && data.daily.time) {
        this.forecastData = data.daily.time.slice(1, 6).map((date, index) => {
          const code = data.daily.weather_code[index + 1]
          return {
            date: this.formatDate(date),
            tempMax: Math.round(data.daily.temperature_2m_max[index + 1]),
            tempMin: Math.round(data.daily.temperature_2m_min[index + 1]),
            description: weatherDescriptions[code] || 'Êú™Áü•',
            icon: this.getWeatherIcon(code)
          }
        })
      }
    },

    getWeatherIcon(weatherCode) {
      // Ê†πÊçÆÂ§©Ê∞î‰ª£Á†ÅËøîÂõûÂõæÊ†áURLÔºà‰ΩøÁî® Open-Meteo Êé®ËçêÁöÑÂõæÊ†áÊúçÂä°Ôºâ
      // ËøôÈáå‰ΩøÁî®‰∏Ä‰∏™ÁÆÄÂçïÁöÑÂ§©Ê∞îÂõæÊ†áÊò†Â∞Ñ
      const iconMap = {
        0: '‚òÄÔ∏è', 1: 'üå§Ô∏è', 2: '‚õÖ', 3: '‚òÅÔ∏è',
        45: 'üå´Ô∏è', 48: 'üå´Ô∏è',
        51: 'üå¶Ô∏è', 53: 'üå¶Ô∏è', 55: 'üåßÔ∏è',
        56: 'üå®Ô∏è', 57: 'üå®Ô∏è',
        61: 'üå¶Ô∏è', 63: 'üåßÔ∏è', 65: 'üåßÔ∏è',
        66: 'üå®Ô∏è', 67: 'üå®Ô∏è',
        71: 'üå®Ô∏è', 73: 'üå®Ô∏è', 75: 'üå®Ô∏è',
        77: 'üå®Ô∏è',
        80: 'üå¶Ô∏è', 81: 'üåßÔ∏è', 82: 'üåßÔ∏è',
        85: 'üå®Ô∏è', 86: 'üå®Ô∏è',
        95: '‚õàÔ∏è', 96: '‚õàÔ∏è', 99: '‚õàÔ∏è'
      }
      return iconMap[weatherCode] || '‚òÄÔ∏è'
    },

    formatDate(dateString) {
      const date = new Date(dateString)
      const month = date.getMonth() + 1
      const day = date.getDate()
      const weekdays = ['Âë®Êó•', 'Âë®‰∏Ä', 'Âë®‰∫å', 'Âë®‰∏â', 'Âë®Âõõ', 'Âë®‰∫î', 'Âë®ÂÖ≠']
      const weekday = weekdays[date.getDay()]
      return `${month}Êúà${day}Êó• ${weekday}`
    },

    getClothingAdvice(temperature, weatherCode, windSpeed, humidity = null) {
      // Ê†πÊçÆÊ∏©Â∫¶„ÄÅÂ§©Ê∞îÁä∂ÂÜµÂíåÈ£éÈÄüÊèê‰æõÁ©øË°£Âª∫ËÆÆ
      const isRainy = [51, 53, 55, 56, 57, 61, 63, 65, 66, 67, 80, 81, 82].includes(weatherCode)
      const isSnowy = [71, 73, 75, 77, 85, 86].includes(weatherCode)
      const isStormy = [95, 96, 99].includes(weatherCode)
      const isWindy = windSpeed > 5 // È£éÈÄüÂ§ß‰∫é5m/sÁÆóÂ§ßÈ£é
      
      let advice = {
        level: '', // ‰øùÊöñÁ≠âÁ∫ßÔºöÂæàÂÜ∑„ÄÅÂÜ∑„ÄÅÂáâÁàΩ„ÄÅËàíÈÄÇ„ÄÅÊ∏©Êöñ„ÄÅÁÇéÁÉ≠
        outfit: [], // Êé®ËçêÁ©øÊê≠
        tips: [] // È¢ùÂ§ñÊèêÁ§∫
      }

      // Ê†πÊçÆÊ∏©Â∫¶Âà§Êñ≠
      if (temperature <= 0) {
        advice.level = 'ÂæàÂÜ∑'
        advice.outfit = ['ÂéöÁæΩÁªíÊúç', '‰øùÊöñÂÜÖË°£', 'ÊØõË°£', 'ÂéöË£§Â≠ê', '‰øùÊöñÈù¥', 'Âõ¥Â∑æ', 'ÊâãÂ•ó', 'Â∏ΩÂ≠ê']
        advice.tips = ['Ê≥®ÊÑè‰øùÊöñÔºåÈÅøÂÖçÈïøÊó∂Èó¥Êà∑Â§ñÊ¥ªÂä®']
      } else if (temperature <= 10) {
        advice.level = 'ÂÜ∑'
        advice.outfit = ['ÁæΩÁªíÊúçÊàñÂéöÂ§ñÂ•ó', 'ÈïøË¢ñTÊÅ§ÊàñÊØõË°£', 'ÈïøË£§', 'ËøêÂä®ÈûãÊàñÈù¥Â≠ê']
        if (isWindy) {
          advice.outfit.push('Èò≤È£éÂ§ñÂ•ó')
          advice.tips.push('È£éÂäõËæÉÂ§ßÔºåÂª∫ËÆÆÁ©øÈò≤È£éË°£Áâ©')
        }
      } else if (temperature <= 20) {
        advice.level = 'ÂáâÁàΩ'
        advice.outfit = ['ËñÑÂ§ñÂ•óÊàñÈ£éË°£', 'ÈïøË¢ñTÊÅ§', 'ÈïøË£§', 'ËøêÂä®Èûã']
        if (isWindy) {
          advice.outfit.push('ËñÑÂ§ñÂ•ó')
          advice.tips.push('ÊúâÈ£éÔºåÂª∫ËÆÆÂä†‰ª∂ËñÑÂ§ñÂ•ó')
        }
      } else if (temperature <= 25) {
        advice.level = 'ËàíÈÄÇ'
        advice.outfit = ['ËñÑÈïøË¢ñÊàñÁü≠Ë¢ñ', 'ËñÑÈïøË£§Êàñ‰πùÂàÜË£§', 'ËøêÂä®ÈûãÊàñ‰ºëÈó≤Èûã']
        advice.tips = ['Â§©Ê∞îËàíÈÄÇÔºåÈÄÇÂêàÊà∑Â§ñÊ¥ªÂä®']
      } else if (temperature <= 30) {
        advice.level = 'Ê∏©Êöñ'
        advice.outfit = ['Áü≠Ë¢ñTÊÅ§', 'Áü≠Ë£§ÊàñËñÑÈïøË£§', 'ÂáâÈûãÊàñËøêÂä®Èûã']
        advice.tips = ['Â§©Ê∞îËæÉÁÉ≠ÔºåÊ≥®ÊÑèÈò≤Êôí']
      } else {
        advice.level = 'ÁÇéÁÉ≠'
        advice.outfit = ['Áü≠Ë¢ñTÊÅ§', 'Áü≠Ë£§', 'ÂáâÈûã', 'ÈÅÆÈò≥Â∏Ω']
        advice.tips = ['Â§©Ê∞îÁÇéÁÉ≠ÔºåÂª∫ËÆÆÁ©øËΩªËñÑÈÄèÊ∞îÁöÑË°£Áâ©', 'Ê≥®ÊÑèÈò≤ÊöëÈôçÊ∏©']
      }

      // Ê†πÊçÆÂ§©Ê∞îÁä∂ÂÜµË∞ÉÊï¥
      if (isRainy) {
        advice.outfit.push('Èõ®Ë°£ÊàñÈõ®‰ºû')
        advice.tips.push('ÊúâÈõ®ÔºåËÆ∞ÂæóÂ∏¶Èõ®ÂÖ∑')
      }
      
      if (isSnowy) {
        advice.outfit.push('Èò≤ÊªëÈù¥')
        advice.tips.push('ÊúâÈõ™ÔºåÊ≥®ÊÑèÈò≤Êªë')
      }
      
      if (isStormy) {
        advice.tips.push('ÊúâÈõ∑Êö¥ÔºåÂª∫ËÆÆÈÅøÂÖçÊà∑Â§ñÊ¥ªÂä®')
      }

      // Ê†πÊçÆÊπøÂ∫¶Ë∞ÉÊï¥ÔºàÈ´òÊπøÂ∫¶Êó∂‰ΩìÊÑüÊõ¥ÂÜ∑ÊàñÊõ¥ÁÉ≠Ôºâ
      if (humidity !== null && humidity > 80) {
        if (temperature < 20) {
          advice.tips.push('ÊπøÂ∫¶ËæÉÈ´òÔºå‰ΩìÊÑü‰ºöÊõ¥ÂÜ∑ÔºåÂª∫ËÆÆÂ§öÁ©ø‰∏Ä‰ª∂')
        } else if (temperature > 25) {
          advice.tips.push('ÊπøÂ∫¶ËæÉÈ´òÔºå‰ΩìÊÑü‰ºöÊõ¥ÁÉ≠ÔºåÊ≥®ÊÑèÈò≤Êöë')
        }
      }

      return advice
    },

    getWeatherCodeFromOpenWeather(main, id) {
      // Â∞ÜOpenWeatherMapÁöÑÂ§©Ê∞î‰ª£Á†ÅËΩ¨Êç¢‰∏∫Open-MeteoÊ†ºÂºè
      // OpenWeatherMapÂ§©Ê∞îIDËåÉÂõ¥Ôºö2xx=Èõ∑Êö¥, 3xx=ÊØõÊØõÈõ®, 5xx=Èõ®, 6xx=Èõ™, 7xx=Â§ßÊ∞îÁé∞Ë±°, 800=Êô¥Êúó, 80x=‰∫ë
      if (id >= 200 && id < 300) return 95 // Èõ∑Êö¥
      if (id >= 300 && id < 400) return 51 // ÊØõÊØõÈõ®
      if (id >= 500 && id < 600) {
        if (id < 520) return 61 // Â∞èÈõ®
        if (id < 530) return 63 // ‰∏≠Èõ®
        return 65 // Â§ßÈõ®
      }
      if (id >= 600 && id < 700) {
        if (id < 620) return 71 // Â∞èÈõ™
        if (id < 630) return 73 // ‰∏≠Èõ™
        return 75 // Â§ßÈõ™
      }
      if (id >= 700 && id < 800) {
        if (id === 701 || id === 741) return 45 // Èõæ
        return 48 // ÂÖ∂‰ªñÂ§ßÊ∞îÁé∞Ë±°
      }
      if (id === 800) return 0 // Êô¥Êúó
      if (id === 801) return 1 // Â∞ë‰∫ë
      if (id === 802) return 2 // ÈÉ®ÂàÜÂ§ö‰∫ë
      return 3 // Â§ö‰∫ëÊàñÈò¥Â§©
    },

    async fetchOpenWeatherMap() {
      // ‰ΩøÁî® OpenWeatherMap APIÔºàÈúÄË¶ÅAPI keyÔºâ
      const city = encodeURIComponent(this.searchCity)
      const currentUrl = `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${this.apiKey}&units=metric&lang=zh_cn`
      const forecastUrl = `https://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=${this.apiKey}&units=metric&lang=zh_cn`

      const [currentResponse, forecastResponse] = await Promise.all([
        axios.get(currentUrl, { timeout: 10000 }),
        axios.get(forecastUrl, { timeout: 10000 })
      ])

      const current = currentResponse.data
      const forecast = forecastResponse.data

      const temp = Math.round(current.main.temp)
      const weatherCode = this.getWeatherCodeFromOpenWeather(current.weather[0].main, current.weather[0].id)
      
      this.weatherData = {
        city: current.name,
        country: current.sys.country,
        temperature: temp,
        feelsLike: Math.round(current.main.feels_like),
        description: current.weather[0].description,
        humidity: current.main.humidity,
        windSpeed: current.wind.speed,
        pressure: current.main.pressure,
        visibility: (current.visibility / 1000).toFixed(1),
        icon: `https://openweathermap.org/img/wn/${current.weather[0].icon}@2x.png`,
        weatherCode: weatherCode,
        clothingAdvice: this.getClothingAdvice(temp, weatherCode, current.wind.speed, current.main.humidity)
      }

      // Â§ÑÁêÜÊú™Êù•Âá†Â§©ÁöÑÈ¢ÑÊä•
      const dailyForecast = {}
      forecast.list.forEach(item => {
        const date = item.dt_txt.split(' ')[0]
        if (!dailyForecast[date]) {
          dailyForecast[date] = {
            date: date,
            tempMax: item.main.temp_max,
            tempMin: item.main.temp_min,
            description: item.weather[0].description,
            icon: `https://openweathermap.org/img/wn/${item.weather[0].icon}@2x.png`
          }
        } else {
          dailyForecast[date].tempMax = Math.max(dailyForecast[date].tempMax, item.main.temp_max)
          dailyForecast[date].tempMin = Math.min(dailyForecast[date].tempMin, item.main.temp_min)
        }
      })

      this.forecastData = Object.values(dailyForecast).slice(0, 5).map(day => ({
        ...day,
        tempMax: Math.round(day.tempMax),
        tempMin: Math.round(day.tempMin)
      }))
    },

    async getCurrentLocation() {
      if (!navigator.geolocation) {
        this.error = 'ÊÇ®ÁöÑÊµèËßàÂô®‰∏çÊîØÊåÅÂú∞ÁêÜ‰ΩçÁΩÆÂäüËÉΩ'
        return
      }

      this.loading = true
      this.error = null

      navigator.geolocation.getCurrentPosition(
        async (position) => {
          try {
            const { latitude, longitude } = position.coords
            // ‰ΩøÁî®ÈÄÜÂú∞ÁêÜÁºñÁ†ÅËé∑ÂèñÂüéÂ∏ÇÂêçÁß∞
            const response = await axios.get(
              `https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${latitude}&longitude=${longitude}&localityLanguage=zh`,
              { timeout: 10000 }
            )
            const city = response.data.city || response.data.locality || 'ÂΩìÂâç‰ΩçÁΩÆ'
            this.searchCity = city
            await this.searchWeather()
          } catch (err) {
            // Â¶ÇÊûúÈÄÜÂú∞ÁêÜÁºñÁ†ÅÂ§±Ë¥•ÔºåÁõ¥Êé•‰ΩøÁî®ÂùêÊ†áÊü•ËØ¢Â§©Ê∞î
            this.error = 'Êó†Ê≥ïËé∑ÂèñÂüéÂ∏ÇÂêçÁß∞ÔºåËØ∑ÊâãÂä®ËæìÂÖ•ÂüéÂ∏ÇÂêçÁß∞'
            this.loading = false
          }
        },
        () => {
          this.error = 'Êó†Ê≥ïËé∑ÂèñÊÇ®ÁöÑ‰ΩçÁΩÆÔºåËØ∑ÂÖÅËÆ∏‰ΩçÁΩÆÊùÉÈôêÊàñÊâãÂä®ËæìÂÖ•ÂüéÂ∏ÇÂêçÁß∞'
          this.loading = false
        }
      )
    },

    retry() {
      this.error = null
      if (this.searchCity) {
        this.searchWeather()
      }
    },

    getLevelClass(level) {
      // Ê†πÊçÆÊ∏©Â∫¶Á≠âÁ∫ßËøîÂõûÂØπÂ∫îÁöÑCSSÁ±ª
      const levelMap = {
        'ÂæàÂÜ∑': 'level-very-cold',
        'ÂÜ∑': 'level-cold',
        'ÂáâÁàΩ': 'level-cool',
        'ËàíÈÄÇ': 'level-comfortable',
        'Ê∏©Êöñ': 'level-warm',
        'ÁÇéÁÉ≠': 'level-hot'
      }
      return levelMap[level] || 'level-comfortable'
    }
  }
}
</script>

